@keyframes lds-double-ring {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes lds-double-ring {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @keyframes lds-double-ring_reverse {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(-360deg);
      transform: rotate(-360deg);
    }
  }
  @-webkit-keyframes lds-double-ring_reverse {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(-360deg);
      transform: rotate(-360deg);
    }
  }
  .lds-css {
    margin: 0 auto;
  }
  
  .lds-double-ring {
    position: relative;
    margin: 0 auto;
  }
  .lds-double-ring div {
    position: absolute;
    position: absolute;
    width: 100px;
    height: 100px;
    top: 50px;
    left: 50px;
    border-radius: 50%;
    border: 4px solid #000;
    border-color: #fe0000 transparent #fe0000 transparent;
    -webkit-animation: lds-double-ring 1.6s linear infinite;
    animation: lds-double-ring 1.6s linear infinite;
  }
  .lds-double-ring div:nth-child(2) {
    width: 88px;
    height: 88px;
    top: 56px;
    left: 56px;
    border-color: transparent #0d730c transparent #0d730c;
    -webkit-animation: lds-double-ring_reverse 1.6s linear infinite;
    animation: lds-double-ring_reverse 1.6s linear infinite;
  }
  .lds-double-ring {
    width: 200px !important;
    height: 200px !important;
    -webkit-transform: translate(-100px, -100px) scale(1) translate(100px, 100px);
    transform: translate(-100px, -100px) scale(1) translate(100px, 100px);
  }
